<template>
  <div id="app">
    <div id="menu">
      <el-menu
        :default-active="activeFunction"
        mode="horizontal"
        :unique-opened="true"
        :router="true"
      >
        <el-menu-item index="/">HOME</el-menu-item>
        <el-menu-item index="/memo">MEMO</el-menu-item>
        <el-menu-item index="/password">PASSWORD</el-menu-item>
        <el-menu-item index="/diary">DIARY</el-menu-item>
        <el-menu-item>
          <a href="https://b.shaneu.cn" target="blank">BLOG</a>
        </el-menu-item>
        <el-menu-item>
          <a href="https://b.shaneu.cn" target="blank">DOWNLOAD</a>
        </el-menu-item>
        <el-menu-item>
          <a href="https://b.shaneu.cn" target="blank">UPLOAD</a>
        </el-menu-item>
      </el-menu>
      <router-view />
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  computed: {
    activeFunction() { return this.$route.path }
  }
}
</script>

<style scoped>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



